import React from 'react';
import { BrowserRouter, NavLink, Route } from 'react-router-dom';

import Home from './pages/home/Home';
import About from './pages/about/About';
import Skills from './pages/skills/Skills';
import Contact from './pages/contact/Contact';
import NavBarItem from './components/NavBarItem';

import { HomeIcon } from './res/home.svg';
import { AboutIcon } from './res/about.svg';
import { SkillsIcon } from './res/skills.svg';
import { ContactIcon } from './res/contact.svg';


import './css/App.css'
import './css/Page.css'

class App extends React.Component{
  render() {
    return (
      <BrowserRouter>
        <div className="app">
          <div className="nav-bar">
            <NavLink exact className="nav-bar-item" to="/"><NavBarItem icon={ HomeIcon } page="Home"/></NavLink>
            <NavLink className="nav-bar-item" to="/about"><NavBarItem icon={ AboutIcon } page="About"/></NavLink>
            <NavLink className="nav-bar-item" to="/skills"><NavBarItem icon={ SkillsIcon } page="Skills"/></NavLink>
            <NavLink className="nav-bar-item" to="/contact"><NavBarItem icon={ ContactIcon } page="Contact"/></NavLink>
          </div>
          <div className="content">
            <Route exact path="/" component={ Home }/>
            <Route path="/about" component={ About }/>
            <Route path="/skills" component={ Skills }/>
            <Route path="/contact" component={ Contact }/>
          </div>
        </div>  
      </BrowserRouter>
    )
  }
}

export default App;
